@import "../../assets/style/app";

.logs {
  @include borderBlock;

  .blockTitle {
    @include blockTitle;
  }

  .table {
    margin-top: 12px;

    .tableLibel {
      padding: 4px 8px;
      background: #2e7d32;
      color: #FFF;
      box-sizing: border-box;
      border-radius: 4px;
    }

    .header {
      margin-top: 4px;
      display: grid;

      grid-column-gap: 4px;

      p {
        padding: 4px 8px;
        background: royalblue;
        color: #FFF;
        box-sizing: border-box;
        border-radius: 4px;
      }

      &_buy {
        grid-template-columns: 2fr 1fr 1fr;
      }

      &_mint {
        grid-template-columns: 1fr 1fr;
      }

      &_price {
        grid-template-columns: 1fr 1fr 1fr;
      }
    }

    .rows {
      margin-top: 2px;

      .row {
        display: grid;
        grid-column-gap: 4px;

        &:not(:first-child) {
          margin-top: 2px;
        }

        &_buy {
          grid-template-columns: 2fr 1fr 1fr;
        }

        &_mint {
          grid-template-columns: 1fr 1fr;
        }

        &_price {
          grid-template-columns: 1fr 1fr 1fr;
        }

        p {
          padding: 4px 8px;
          background: #333;
          color: #FFF;
          box-sizing: border-box;
          border-radius: 4px;
        }
      }
    }
  }
}
